import React, { useEffect, useRef, useState } from "react";
import "./cube.scss";

const isBrowser = typeof window !== "undefined";

export default function Cube({ dark, id, children }) {
  const [moving, setMoving] = useState(50);
  const ref = useRef(null);

  useEffect(() => {
    if (!isBrowser || !ref.current) return;

    const handleScroll = () => {
      if (!ref.current) return;

      const element = ref.current;
      const rect = element.getBoundingClientRect();

      const heightw = window.innerHeight;
      const var1 = -rect.height / 100;
      const var2 = rect.y / var1 + (heightw / rect.height) * 50;

      if (var2 <= 150 && var2 >= -150) {
        setMoving(var2);
      }
    };

    window.addEventListener("scroll", handleScroll);
    handleScroll(); // inicial

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  if (!isBrowser) return null;

  return (
    <section
      id={id}
      className={dark ? "section darkcube" : "section"}
      ref={ref}
    >
      <div className="screen" style={{ perspectiveOrigin: `50% ${moving}%` }}>
        <div className="childrendiv">{children}</div>
        <div className="room">
          <div className="lateral_left" />
          <div className="lateral_front" />
          <div className="lateral_right" />
        </div>
      </div>
    </section>
  );
}
